* {
	box-sizing: border-box;
}

.main-wrapper {
	display: flex;
	flex-direction: column;
}

.main-wrapper>div {
	border: 1px solid #000;
	margin: 5px;
}

.seasons-wrapper {
	flex: 1;
	display: flex;
	justify-content: center;
}

.seasons-wrapper>div {
	margin: 10px;
	cursor: pointer;
	border: 1px solid #000;
	border-radius: 5px;
	padding: 5px;
}

.outer-wrapper {
	/* flex: 3; */
	display: flex;
}

.dress-wrapper {
	flex: 1;
	display: flex;
	justify-content: space-around;
	/* flex-direction: column; */
}

.dress-wrapper div {
	/* border: 1px solid #000; */
	box-sizing: border-box;
	position: relative;
	z-index: 5;
}

.dress-card-wrapper {
	display: flex;
	/* align-items: baseline; */
	flex-direction: column;
}

.dress-card-wrapper div {
	cursor: pointer;
	margin: 5px 0;
}

.dress-card-wrapper div:nth-child(n+2) {
	border: 1px solid grey;
	box-shadow: 0 0 3px grey;
	border-radius: 10px;
	padding: 5px;
	height: 160px;
}

.dress-card-wrapper img {
	height: 100%;
}

button {
	display: block;
	width: 100%;
}

.main-photo-wrapper {
	position: relative;
	padding: 45px;
}

.main-photo-wrapper>div {
	position: absolute;
	z-index: 3;
}

.dressed-clothes {
	position: absolute;
	top: 6px;
	left: 3px;
	z-index: 2;
}

#sword {
	transform: scale(-1, 1);
	left: 115px;
	top: 405px;
}

#left {
	top: 352px;
	left: 167px;
}

#right {
	top: 356px;
	left: 351px;
}

